  z-index: 9999;
  position: relative;
  background: #f8f8f8;
  padding-top: 45px;
}
.bottom-nav {
  display: none;
}
// shop
.shop-item {
  margin-bottom: 22px;
}

.shop-title {
  font-size: 12px;
  margin-bottom: 10px;
  position: relative;
}

.shop-title .badge {
  display: inline-block;
  background-color: #fc5959;
  text-align: center;
  color: #fff;
  border-radius: 3px;
  padding: 2px 4px;
  margin-right: 3px;
  font-size: 10px;
  line-height: 10px;
  vertical-align: middle;
  margin-bottom: 4px;
}

.shop-title .follows {
  color: silver;
  margin-left: 4px;
}

.shop-images {
  margin-right: 10px;
  font-size: 0;
}

.shop-images li {
  position: relative;
  width: 32%;
  box-sizing: border-box;
  margin-right: 2%;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
}

.shop-images li:after {
  content: " ";
  display: block;
  padding-top: 100%;
}

.shop-images li:last-child {
  margin-right: 0;
}

.shop-images li img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 100%;
  max-height: 100%;
  margin: auto;
}
// shop end

.wakeup-app {
  position: fixed;
  top: -60px;
  left: 0;
  z-index: 100;
  background-color: #ececec;
  width: 100%;
  height: 60px;
  cursor: pointer;
  text-align: center;
  overflow: hidden;
  transition: all 0.5s linear;
}

.wakeup-app .close {
  float: left;
  width: 40px;
  height: 60px;
  position: relative;
  cursor: pointer;
}

.wakeup-app .close:after {
  position: absolute;
  display: block;
  content: "\D7";
  width: 18px;
  height: 18px;
  font-size: 14px;
  line-height: 16px;
  border-radius: 9px;
  color: #fff;
  background-color: #dddedf;
  top: 50%;
  left: 50%;
  margin-top: -9px;
  margin-left: -9px;
}

.wakeup-app .app-icon {
  float: left;
  width: 60px;
  height: 60px;
  background-image: url("/v2/image/wap/weixin_share_default_logo.png");
  background-size: 80% 80%;
  background-repeat: no-repeat;
  background-position: 50%;
}

.wakeup-app .description {
  float: left;
  text-align: left;
  padding: 5px 0 5px 3px;
}

.wakeup-app .description .title {
  margin: 6px 0;
  font-size: 15px;
  line-height: 18px;
  font-weight: 400;
}

.wakeup-app .description .meta {
  color: #999;
  line-height: 1;
  font-size: 12px;
  letter-spacing: 1px;
}

.wakeup-app .btn {
  float: right;
  margin-top: 12px;
  margin-right: 12px;
  padding: 8px;
  border-color: #fc5959;
  background-color: #fc5959;
  letter-spacing: 1px;
}

.wakeup-app-mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: #000;
  opacity: 0.8;
  z-index: 101;
}

.wakeup-app-opentip {
  position: fixed;
  height: 144px;
  top: 0;
  left: 10px;
  right: 10px;
  z-index: 102;
}

.wakeup-app-opentip .lay {
  width: 100%;
  height: 120px;
  background-color: #f9f9f9;
  border-radius: 0 0 8px 8px;
  background: #f9f9f9 url("/v2/image/wap/opentip.png") 100% 4px no-repeat;
  background-size: 127px auto;
}

.wakeup-app-opentip .note {
  float: right;
  margin: 39px 58px 0 0;
  width: 160px;
  line-height: 20px;
}

.wakeup-app-opentip .note h3 {
  font-size: 20px;
  margin-bottom: 6px;
  font-weight: 400;
}

.wakeup-app-opentip .note p {
  font-size: 14px;
}

.bottom-nav {
  position: fixed;
  width: 100%;
  height: 50px;
  bottom: 0;
  background-color: #fff;
  z-index: 999;
  -o-border-image: url("https://b.yzcdn.cn/v2/image/wap/border-line-2.png") 2
    stretch;
  border-image: url("https://b.yzcdn.cn/v2/image/wap/border-line-2.png") 2
    stretch;
  border-top: 2px solid #e5e5e5;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (min--moz-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  .bottom-nav {
    border-top-width: 1px;
  }
}

.bottom-nav ul {
  position: relative;
  height: 100%;
  zoom: 1;
}

.bottom-nav ul:after {
  content: "";
  display: table;
  clear: both;
}

.bottom-nav li {
  display: block;
  float: left;
  width: 25%;
  height: 100%;
}

.bottom-nav a {
  display: block;
  width: 100%;
  height: 100%;
  padding-top: 8px;
  text-align: center;
  font-size: 10px;
}

.bottom-nav [class^="icon-"] {
  display: block;
  width: 22px;
  height: 22px;
  margin: auto;
  margin-bottom: 4px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.bottom-nav .icon-home {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .icon-category {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .icon-cart {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .icon-user {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .icon-sign {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .active a {
  color: #ff6366;
}

.bottom-nav .active .icon-home {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .active .icon-category {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .active .icon-cart {
  background-image: url("data:image/png;base64,");
}

.bottom-nav .active .icon-user {
  background-image: url("data:image/png;base64,");
}


body,
html {
  -webkit-tap-highlight-color: transparent;
}

img {
  max-width: 100%;
  max-height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.container {
  background-color: #f4f4f4;
}

.center-img {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  width: 60px;
  height: 60px;
}

.center-img img {
  max-width: 100%;
  max-height: 100%;
  border: 0;
  vertical-align: middle;
}

.goods-item {
  background: #fff;
}

.goods-item .thumb {
  margin-bottom: 6px;
  text-align: center;
  background-color: #f4f4f4;
  position: relative;
}

.goods-item .thumb:after {
  content: " ";
  display: block;
  padding-top: 100%;
}

.goods-item .thumb img {
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: 100%;
  max-height: 100%;
}

.goods-item .thumb .sell-out {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: url("https://b.yzcdn.cn/v2/image/yzapp/h5/2listicon_shouqing@2x.png")
    no-repeat 50%;
  background-size: 40% auto;
  background-color: rgba(0, 0, 0, 0.3);
}

.goods-item .thumb .sell-out-34 {
  background-size: 34% auto;
}

.goods-item .thumb .sell-out-50 {
  background-size: 50% auto;
}

.goods-item .thumb .sell-out-60 {
  background-size: 60% auto;
}

.goods-item .detail .title {
  padding-bottom: 8px;
  font-size: 12px;
  line-height: 1;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.goods-item .detail .price {
  padding-bottom: 8px;
  font-size: 14px;
  color: #ff525e;
}

.container {
  overflow-y: hidden;
  position: relative;
  box-sizing: border-box;
}

.content {
  box-sizing: border-box;
  width: 100%;
}

.list-finished,
.loading-more {
  width: 100%;
  padding: 20px 10px;
  vertical-align: middle;
  text-align: center;
  color: #999;
  font-size: 12px;
  line-height: 20px;
  box-sizing: border-box;
}

.list-finished span,
.loading-more span {
  display: inline-block;
  margin-top: 10px;
  width: 16px;
  height: 16px;
  background: url("/v2/image/loader.gif") no-repeat 50%;
  background-size: 16px 16px;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (min--moz-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  .list-finished span,
  .loading-more span {
    background-image: url("/v2/image/loader@2x.gif");
  }
}

.empty-item-block {
  padding: 30px;
  text-align: center;
}

.empty-item-block .empty-item-title {
  line-height: 60px;
}

.empty-item-block .empty-item-go {
  margin-top: 20px;
  display: inline-block !important;
  padding: 8px 20px;
  border: 1px solid #ff5050;
  color: #ff5050;
  border-radius: 3px;
}

.img-wrap {
  width: 100%;
  position: relative;
  padding-top: 100%;
  background-color: #e3e2e2;
}

.img-wrap img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 100%;
  max-height: 100%;
  margin: auto;
}

.switch-show-type {
  padding: 10px;
  text-align: right;
}

.switch-show-type a {
  padding: 0 5px;
}

.switch-show-type a.active {
  color: #999;
}

.mt20 li:first-child,
.record-broswe-block .record-time {
  padding-top: 20px;
}

.record-broswe-block ul {
  background-color: #fff;
}

.record-broswe-block .record-time,
.record-broswe-block li {
  padding-left: 10px;
  margin-bottom: 5px;
}

.record-broswe-block li {
  position: relative;
  margin-bottom: 3px;
}

.record-broswe-block li .img-wrap {
  float: left;
  width: 100px;
  height: 100px;
  padding-top: 0;
}

.record-broswe-block li .detail {
  height: 105px;
  margin-left: 115px;
  box-sizing: border-box;
  padding: 10px 15px 0 0;
  -o-border-image: url("/v2/image/wap/border-line.png") 2 stretch;
  border-image: url("/v2/image/wap/border-line.png") 2 stretch;
  border-bottom: 2px solid #fe6573;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (min--moz-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  .record-broswe-block li .detail {
    border-bottom-width: 1px;
  }
}

.record-broswe-block li .detail .title {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  margin-bottom: 20px;
  font-size: 16px;
}

.record-broswe-block li .detail .price {
  font-size: 18px;
  color: #ff525e;
}

.store-item-block {
  display: block;
  padding-top: 10px;
  margin-top: 20px;
  background-color: #fff;
  -o-border-image: url("/v2/image/wap/border-line.png") 2 stretch;
  border-image: url("/v2/image/wap/border-line.png") 2 stretch;
  border-top: 2px solid #fe6573;
  border-bottom: 2px solid #fe6573;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (min--moz-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  .store-item-block {
    border-top-width: 1px;
    border-bottom-width: 1px;
  }
}

.store-item-block .store-item-header {
  padding: 0 10px 10px;
}

.store-item-block .store-item-header .store-logo {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid #d8d8d8;
}

.store-item-block .store-item-header .store-logo img {
  width: 100%;
  border-radius: 30px;
}

.store-item-block .store-item-header .store-info {
  margin-left: 58px;
}

.store-item-block .store-item-header .store-info .store-name {
  position: relative;
  margin-bottom: 6px;
  padding-top: 5px;
}

.store-item-block .store-item-header .store-info .store-name:after {
  content: " ";
  clear: both;
  display: inline-block;
  width: 0;
  height: 0;
  overflow: hidden;
}

.store-item-block .store-item-header .store-info .store-name .store-name-text {
  display: block;
  float: left;
  vertical-align: middle;
  font-size: 16px;
  padding-bottom: 2px;
}

.store-item-block .store-item-header .store-info .store-name .ouath-icon {
  display: block;
  float: left;
  vertical-align: middle;
  margin-left: 10px;
  width: 14px;
  height: 14px;
  background: url("/v2/image/yzapp/h5/icon_attestation@2x.png") no-repeat 50%;
  background-size: auto 100%;
}

.store-item-block .store-item-content {
  padding: 0 15px;
}

.store-item-block .store-item-content li {
  position: relative;
  width: 32%;
  margin-right: 2%;
  float: left;
  background: #fff;
  margin-bottom: 15px;
  box-sizing: border-box;
}

.store-item-block .store-item-content li:nth-child(3n) {
  margin-right: 0;
}

.store-item-block .store-item-content li .img-wrap {
  height: 0;
  padding-top: 100%;
  position: relative;
}

.store-item-block .store-item-content li .img-wrap img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
}

.store-item-block .store-item-content.feeds-single-img li {
  width: auto;
  margin-right: 0;
}

.store-item-block .store-item-content.feeds-single-img li .img-wrap {
  height: auto;
  padding-top: 0;
  position: static;
}

.store-item-block .store-item-content.feeds-single-img li .img-wrap img {
  position: static;
}

.store-item-block .store-btn-goto {
  float: right;
  font-size: 12px;
  margin-top: 10px;
  margin-right: 6px;
  color: #ff6c6c;
}

.none-float {
  float: none !important;
}

.btn-goto-shop {
  position: relative;
  padding: 6px 10px;
}

.btn-goto-shop:after {
  border-radius: 5px;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  border: 1px solid #fe6573;
}

.search-store-item-block {
  margin-top: 10px;
}

@media (min-width: 320px) {
  .store-item-block .store-item-header .store-info .store-name-text {
    max-width: 170px;
  }
}

.tabs {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 45px;
  top: 0;
  background: #fff;
  color: #333;
  border-bottom: 1px solid #eee;
  text-align: center;
  z-index: 100;
}

.tabs .tab-name {
  width: 50%;
  height: 45px;
  font-size: 16px;
  line-height: 45px;
}

.tabs .tab-name.active {
  color: #fc5959;
  border-bottom: 1px solid #fc5959;
}

.tab-content {
  margin-top: 66px;
}

.membercards-list-block {
  padding: 28px 10px;
}

.membercards-list-block .membercard-item {
  width: 100%;
  height: 68px;
  box-sizing: border-box;
  border-radius: 6px 6px 0 0;
  background-color: #55bd47;
  margin-bottom: 15px;
  position: relative;
  -webkit-tap-highlight-color: transparent;
}

.membercards-list-block .avatar {
  width: 44px;
  height: 44px;
  position: absolute;
  left: 20px;
  top: 12px;
  background: #fff;
  border-radius: 50%;
}

.membercards-list-block .info {
  margin: 0 78px;
}

.membercards-list-block .shop-name {
  color: #fff;
  font-size: 16px;
  line-height: 66px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.membercards-list-block .card-num-block {
  display: inline;
  height: 20px;
  position: absolute;
  top: 24px;
  right: 20px;
  padding: 0 10px;
}

.membercards-list-block .card-num-block:after {
  border-radius: 20px;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  pointer-events: none;
  border: 1px solid #fff;
}

.membercards-list-block .cross {
  color: #fff;
  font-size: 9px;
}

.membercards-list-block .card-num {
  color: #fff;
  font-size: 11px;
}

.go-to-top {
  position: fixed;
  right: 15px;
  z-index: 99999;
  bottom: 60px;
  width: 60px;
  height: 60px;
  background: url("data:image/png;base64,")
    no-repeat 50%;
  background-size: 46px;
}

.with-top-search {
  padding-top: 45px;
}

.search-wrap {
  padding: 10px 7px;
  font-size: 0;
}

.search-wrap .goods-item {
  width: 49%;
  margin-bottom: 10px;
  display: inline-block;
}

.search-wrap .goods-item:nth-child(odd) {
  margin-right: 2%;
}

.search-wrap .goods-item .thumb {
  position: relative;
  margin: 0 0 10px;
  background: #fff;
}

.search-wrap .goods-item .thumb:after {
  content: " ";
  display: block;
  padding-top: 100%;
}

.search-wrap .goods-item .thumb img {
  position: absolute;
  margin: auto;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}

.search-wrap .goods-item .detail {
  padding: 0 7px;
  font-size: 14px;
}

.search-wrap .goods-item .title {
  font-size: 14px;
}

.search-wrap .goods-item .meta {
  margin-bottom: 10px;
}

.search-wrap .goods-item .price {
  margin-right: 20px;
}

.search-wrap .goods-item .ship {
  color: silver;
  font-size: 12px;
  line-height: 14px;
}

.search-shop-wrap {
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
}
