




































































































































.custom-search {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  padding: 6px 15px;
  background-color: #fafafa;
  z-index: 1001;
  border-bottom: 2px solid #e5e5e5;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (min--moz-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  .custom-search {
    border-bottom-width: 1px;
  }
}

.custom-search form {
  margin: 0;
  position: relative;
  background: #fff;
  overflow: hidden;
  box-sizing: border-box;
  width: 100%;
}

.custom-search form:before {
  z-index: 1;
  border-radius: 8px;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  pointer-events: none;
  border: 1px solid #e5e5e5;
}

.custom-search form::-webkit-input-placeholder {
  color: #d7d7d7;
}

.custom-search form .custom-search-input {
  margin: 0;
  padding: 0 10px 0 40px;
  -webkit-appearance: none;
  -moz-appearance: none;
  border-radius: 0;
  height: 32px;
  width: 100%;
  box-sizing: border-box;
  border: 0 none;
  outline: none;
  font-size: 14px;
  box-shadow: 0 0 0 0;
}
// focus
.custom-search.focus form {
  float: left;
  width: 88%;
  &:after {
    content: "";
    display: table;
    clear: both;
  }
  .custom-search-input {
    padding-left: 66px;
  }
}

.custom-search.focus:after {
}

.custom-search .cancel {
  display: block;
  margin-left: 8px;
  float: left;
  line-height: 32px;
  font-size: 14px;
  color: #666;
}

.icon-custom-search {
  position: absolute;
  top: 0;
  left: 2px;
  border: 0 none;
  height: 32px;
  line-height: 32px;
  width: 37px;
  padding: 0;
  color: #fff;
  font-size: 14px;
  text-indent: -9999px;
  background: #fff
    url("data:image/png;base64,")
    50% no-repeat;
  background-size: 16px 16px;
  border-radius: 5px 0 0 5px;
}

.custom-search-type-selection-btn {
  position: absolute;
  top: 6px;
  left: 15px;
  border: 0 none;
  font-size: 14px;
  color: #666;
  height: 32px;
  padding-right: 12px;
  padding-left: 12px;
  background: #fff
    url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAICAYAAADN5B7xAAAAAXNSR0IArs4c6QAAAINJREFUGBljnDlz5gIGBoZ4ICYGLGQCqsoC4mtEqAapyWIEKZw3b57W79+/TwOZXCA+FvCNlZXVNCkp6RrIBgYQg5GRMRuLQrAQSA6kBsQBawAx0tLSFgCphSA2GlgIlQMLwzVAFaH7B+xuZAPAfkAWQPIPA8zdyPJY2bNmzUoAYWySAB1bJ40myOHYAAAAAElFTkSuQmCC")
    no-repeat 100%;
  background-size: 6px, 4px;
  display: none;
}

.custom-search-type-selection-btn-focus {
  display: block;
}

.custom-search-button {
  position: absolute;
  top: 0;
  left: 2px;
  border: 0 none;
  height: 32px;
  line-height: 32px;
  width: 37px;
  padding: 0;
  color: #fff;
  font-size: 14px;
  text-indent: -9999px;
  background: #fff
    url("data:image/png;base64,")
    50% no-repeat;
  background-size: 16px 16px;
  border-radius: 5px 0 0 5px;
}

.custom-search-icon {
  background: url("data:image/png;base64,")
    no-repeat 50%;
  background-size: 15px;
  height: 15px;
  width: 15px;
  display: inline-block;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  color: transparent;
}

// 
.search-type-dropdown-menu {
  width: 120px;
  position: absolute;
  top: 32px;
  left: 5px;
  border-radius: 2px;
  z-index: 1001;
}

.search-type-dropdown-menu-indicator {
  height: 12px;
  display: block;
}

.search-type-dropdown-menu-indicator i {
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 12px;
  border-color: transparent transparent #4e4e4e;
  margin-left: 17px;
}

.drop-menu-content {
  background: #4e4e4e;
  border-radius: 4px;
}

.search-type-goods {
  padding: 12px;
}

.search-type-goods span {
  font-size: 16px;
  color: silver;
  margin-left: 14px;
  display: inline-block;
  vertical-align: middle;
}

.search-type-shop {
  padding: 12px;
}

.search-type-shop span {
  font-size: 16px;
  color: silver;
  margin-left: 14px;
  display: inline-block;
  vertical-align: middle;
}

.search-type-dropdown-menu-divider {
  background: #666;
  height: 1px;
}

.icon-goods {
  background: url("data:image/png;base64,")
    no-repeat 50%;
  background-size: contain;
}

.icon-goods,
.icon-shop {
  height: 19px;
  width: 18px;
  vertical-align: middle;
  display: inline-block;
}

.icon-shop {
  background: url("data:image/png;base64,")
    no-repeat 50%;
  background-size: contain;
}
