





















































































































































































































































































































































































































































































































@import url("./cart.scss");
.block-item-cart {
  .name-card {
    .num.static {
      display: block;
    }
    .num.edit {
      display: none;
    }
  }
  &.editing {
    left: 0 !important;
    .name-card {
      .num.static {
        display: none;
      }
      .num.edit {
        display: block;
      }
    }
  }
}

.bottom-cart {
  color: rgb(153, 153, 153);
  .total-price {
    display: block;
  }

  .j-delete-goods {
    display: none;
  }

  .btn {
    border: none;
    background: rgb(241, 241, 241);
  }
  &.isCheck {
    .total-price {
      color: rgb(255, 130, 46);
    }
    .js-go-pay.btn {
      color: #fff;
      background-color: #ed5050;
      border-color: #ed5050;
    }
  }

  &.isEdit {
    .total-price {
      display: none;
    }
    .j-delete-goods {
      display: block;
    }
  }
}
.van-dialog {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 70%;
  font-size: 16px;
  border-radius: 4px;
  background-color: #fff;
  z-index: 2002;
  transform: translate(-50%, -50%);
}
.van-dialog__message {
  line-height: 1.5;
  padding: 15px 20px;
  border-bottom: 1px solid #ccc;
}

.van-dialog__footer--buttons {
  display: flex;
  justify-content: space-around;

  button {
    width: 100%;
    height: 50px;
    line-height: 48px;
    background-color: #fff;
    border-radius: 3px;
    text-align: center;
    box-sizing: border-box;
    color: #333;
    outline: none;
    border: none;

    &:active {
      background-color: #ccc;
    }
    &:last-child {
      color: #00c000;
      border-left: 1px solid #ccc;
    }
  }
}

.van-dialog__footer {
  overflow: hidden;
}

.van-modal {
  z-index: 2001;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
}
