












































.bottom-nav {
  position: fixed;
  width: 100%;
  height: 50px;
  bottom: 0;
  background-color: #fff;
  z-index: 8;
  border-image: url(https://b.yzcdn.cn/v2/image/wap/border-line-2.png) 2 stretch;
  border-top: 2px solid #e5e5e5;

  ul {
    position: relative;
    height: 100%;
    zoom: 1;

    &:after {
      content: "";
      display: table;
      clear: both;
    }

    li {
      display: block;
      float: left;
      width: 25%;
      height: 100%;

      a {
        display: block;
        width: 100%;
        height: 100%;
        padding-top: 8px;
        text-align: center;
        font-size: 10px;
      }
    }
  }
  [class^="icon-"] {
    display: block;
    width: 22px;
    height: 22px;
    margin: auto;
    margin-bottom: 4px;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: 100% auto;
  }
  .icon-home {
    background-image: url("data:image/png;base64,");
  }
  .icon-category {
    background-image: url("data:image/png;base64,");
  }
  .icon-cart {
    background-image: url("data:image/png;base64,");
  }
  .icon-user {
    background-image: url("data:image/png;base64,");
  }
  .router-link-active{
    color: #ff6366;
    .icon-home {
      background-image: url("data:image/png;base64,");
    }
    .icon-category {
      background-image: url("data:image/png;base64,");
    }
    .icon-cart {
      background-image: url("data:image/png;base64,");
    }
    .icon-user {
      background-image: url("data:image/png;base64,");
    }
  }
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
  only screen and (min--moz-device-pixel-ratio: 1.5),
  only screen and (min-device-pixel-ratio: 1.5) {
  .bottom-nav {
    border-top-width: 1px;
  }
}
